import React from 'react';
import { Card, Row, Col, Button } from 'react-bootstrap';
import { Line } from 'react-chartjs-2'; // For the line chart
import { FaUsers, FaShoppingCart, FaDollarSign, FaBox } from 'react-icons/fa';
import Layout from '@/components/Layout';
import styles from './Dashboard.module.css';

const Dashboard = () => {
  const chartData = {
    labels: ['January', 'February', 'March', 'April', 'May'],
    datasets: [
      {
        label: 'Sales Over Time',
        data: [65, 59, 80, 81, 56],
        fill: false,
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1,
      },
    ],
  };

  return (
    <Layout>
      <div className={styles.dashboard}>
        {/* Overview Stats Section */}
        <Row className="mb-4">
          <Col md={3}>
            <Card className="text-center">
              <Card.Body>
                <FaUsers size={40} />
                <Card.Title>Users</Card.Title>
                <Card.Text>1,234</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col md={3}>
            <Card className="text-center">
              <Card.Body>
                <FaShoppingCart size={40} />
                <Card.Title>Orders</Card.Title>
                <Card.Text>567</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col md={3}>
            <Card className="text-center">
              <Card.Body>
                <FaDollarSign size={40} />
                <Card.Title>Revenue</Card.Title>
                <Card.Text>$12,345</Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col md={3}>
            <Card className="text-center">
              <Card.Body>
                <FaBox size={40} />
                <Card.Title>Products</Card.Title>
                <Card.Text>456</Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Chart Section */}
        <Row>
          <Col md={12}>
            <Card>
              <Card.Body>
                <Card.Title>Sales Over Time</Card.Title>
                <Line data={chartData} />
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Recent Activity */}
        <Row className="mt-4">
          <Col md={12}>
            <Card>
              <Card.Body>
                <Card.Title>Recent Activity</Card.Title>
                {/* Activity List */}
                <ul>
                  <li>User1 placed an order</li>
                  <li>User2 updated profile</li>
                  <li>System ran a scheduled backup</li>
                </ul>
                <Button variant="primary">View More</Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </div>
    </Layout>
  );
};

export default Dashboard;
